@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-category-list{
	@bd-color:#eee;

	.category_tit{
		padding: 15*@rex;
		border-bottom: 1*@rex solid @bd-color;
		font-size: 16*@rex;
		background: #fff;
		overflow: hidden;

		h2{
			float:left;
			font-size:100%;
			font-weight:normal;
		}

		.icon_arrow{
			float: right;
			font-size: 12*@rex;
			transition: transform linear .2s;
			color: #999;
		}
	}

	.second_category{
		display:none;
		background: @bd-color;
		padding:10*@rex 10*@rex 0;

		.wrapper{

			.second-tit{
			    font-size: 14*@rex;
			    padding:0 0 15*@rex 10*@rex
			}

			.item{
				width:100%;
				zoom:1;
				overflow:hidden;

				.list{
					width: 33.3%;
					float:left;
					margin-bottom:15*@rex;
					font-size: 13*@rex;

					a{
						display: block;
					    padding: 10*@rex;
					    background-color: #fff;
					    border-radius: 5*@rex;
					    margin: 0 10*@rex;
					    .ellipsis();
					    text-align: center;
					}
				}
			}
		}
	}

	&.active{
		.turnup{
			transform: rotate(180deg);
			transition: transform linear .2s;
		}

		.second_category{
			display: block;
		}
	}
}