@import './_store.less';

@import './module/fn-store-category-list.less';
@import './module/fn-store-loading-btn.less';
@import './module/fn-store-bottom-nav.less';


.@{container-class}{
	background:#fff;

	.header{
		display: flex;
		align-items:center;

		.set_ip{
			margin: 0 15*@rex 0 50*@rex;
		    padding: 0 8*@rex;
		    height: 35*@rex;
		    line-height: 35*@rex;
		    border-radius: 3*@rex;
		    flex: 1;
		    background: #d5d5d5;
		    display: flex;
		    align-items: center;
		    justify-content: center;

		    .search{
		    	margin-right: 5*@rex;
		    	font-size: 16*@rex;
		    	color: #888;
		    	display:block;
		    }

		    .text{
				display: block;
				background-color: inherit;
			    appearance: none;
			    font-size: 14*@rex;
			    flex: 1;
			    border: none;
			    outline:none;
			    color: #333333;
		    }
		}
	}

}










